import React from 'react';
import {message} from 'antd';
import { Link } from 'react-router'; 
import { observer, inject } from 'mobx-react';  
import { browserHistory } from 'react-router'; // 
import MyScroll from 'react-custom-scrollbars';// 
import CalendarChoose from '../calendar-shoose';//
import CommodityIficationPopup from '../commodity-ification-popup';//
import ZjEstimatePopup from '../zj-estimate-popup';////
import GqEstimatePopup from '../estimate-gq-popup';///
import ImportPopup from '../import-popup';//
import RightMemu from '../right-memu';//
import Prompt from 'components/prompt-common';//
import './estimate-clear-total.less'; 
import '../../modal.css'; 

message.config({
	top: 300 
});

 
// 
function SelloutLb({ selloutStore, sellout,handleClick}) { 
	 return <li  onClick={handleClick} className={sellout.selected === true ? 'each-data click-data' : 'each-data'} >
                <span>{sellout.index + 1}</span>
                <span>{sellout.productCode}</span>
                <span>{sellout.productName}</span>
                <span>{sellout.firstCategoryName}</span>
                <span>{sellout.secondCategoryName }</span>
                <span>{sellout.unitName}</span>
                <span>{sellout.optionName}</span>
                <span>{sellout.needWeigh ? sellout.preOrderFloatQuantity : sellout.preOrderQuantity }</span>
                <span>{sellout.needWeigh ? sellout.initFloatQuantity : sellout.initQuantity}</span> 
                <span>{sellout.needWeigh ? sellout.lossFloatQuantity : sellout.lossQuantity }</span>
                <span>{sellout.needWeigh ? sellout.remainingFloatQuantity : sellout.remainingQuantity }</span>
                 <span>{sellout.needWeigh ? sellout.orderedFloatQuantity : sellout.orderedQuantity }</span>
                <span>{(sellout.needWeigh ? sellout.remainingFloatQuantity : sellout.remainingQuantity) > 0 ? "": ""}</span>
                
            </li> 
}
//
@inject('selloutStore') @observer
class EstimateClearTotal extends React.Component {
 

 constructor(props, context) {
		super(props, context);   
        this.state={
            Commodity:'',//
            ZjEstimatePopup:'',//// 
            GqEstimate:'',///
            Menu:'',//
            isnodl:'false',//
            Import:'',//
            addmune: '', //
            recordValue:'',//
            defaultvalue:''
        };   
        let selloutStore=this.props.selloutStore;   
        selloutStore.getselloutList({storeDay:selloutStore.currentdate});//action  0
        
	} 


    componentDidUpdate() {
        let selloutStore=this.props.selloutStore;  
        let feedback = selloutStore.feedback; 
        if(feedback) {
        //
            switch(feedback.status) {
                case 'success': 
                    message.success(feedback.msg,selloutStore.closeFeedback());
                    break;
                case 'warn':
                    message.warn(feedback.msg,selloutStore.closeFeedback());
                    break;
                case 'error':
                    message.warn(feedback.msg,selloutStore.closeFeedback());
                    break;
                default:
                    message.info(feedback.msg,selloutStore.closeFeedback());
            }
        }

         
    }


    //
    clicksellout(sellout) {   
        this.props.selloutStore.getafterobj(sellout);//////action
        this.props.selloutStore.checkedselloutList(sellout.index);//  
    }

    //
    reachValue=(e)=>{
        var value = e.target.value;
        this.setState({recordValue:value});

    }

    //
    operatehistory=(e)=>{
        let selloutStore=this.props.selloutStore;  
        browserHistory.push('/sellout/history'); //
        selloutStore.savelastcombination('0','','0');//
        selloutStore.saveshop='0';//
    }

  render() { 
    let selloutStore=this.props.selloutStore;  
    let feedback = selloutStore.feedback;
    let operatePrompt; 
    if(feedback && feedback.status === 'error') {
      //
      feedback.okClick = () =>{
        if(feedback.confirmClick) feedback.confirmClick();
        feedback.cancelClick();
      }
      feedback.cancelClick = () =>{
        selloutStore.closeFeedback();
      }
      operatePrompt = <Prompt message={feedback} />
    }

    let typename= ''  
    if(selloutStore.onelevel.onelevelname !== ""){
        typename=selloutStore.onelevel.onelevelname;
    }
    if(selloutStore.commoditytype.classifyName !== ""){
        typename=selloutStore.commoditytype.classifyName; 
    }
    


    return (
            <div className="estimate-claer-history-modal">
                <div className="estimate-main">
                    <div className="estimate-main-search">
                        <CalendarChoose 
                            estimeok={(e)=>{ //
                                this.setState({isnodl:e});
                            }}
                             getResult={(e)=>{//
                                selloutStore.getselloutList({
                                    storeDay:e
                                }); 

                            }}
                            activeclick={(e)=>{//
                                selloutStore.getselloutList({
                                    storeDay:e
                                });
                                
                                if(selloutStore.currentdate >e){
                                    selloutStore.getTakeeffect(); 
                                    if(selloutStore.Takeeffect === true){
                                        this.setState({isnodl:'false'});
                                    }else{
                                        this.setState({isnodl:'true'});
                                    }
                                }
                                
                            }}
                            >
                        </CalendarChoose>
                        <div className="search-right">
                            <div className="classification" 
                                onClick={()=>{ 
                                    selloutStore.getcommodityclassify();  
                                    this.setState({Commodity:
                                                   <CommodityIficationPopup 
                                                        closebutton={()=>{
                                                            this.setState({Commodity:''});
                                                        }}
                                                        okbutton={()=>{
                                                            this.setState({Commodity:'',
                                                                            defaultvalue:  selloutStore.lastcombination.lastname === ""  ? 
                                                                                '':
                                                                                selloutStore.lastcombination.lastname
                                                                });
                                                            selloutStore.getselloutList({
                                                                storeDay:selloutStore.currentdate,
                                                                categoryID: selloutStore.lastcombination.lastid === "" 
                                                                            ? '0'
                                                                            : selloutStore.lastcombination.lastid,
                                                            });
                                                        }}>
                                                    </CommodityIficationPopup>
                                                });
                                }}>
                                 {this.state.defaultvalue}
                                <i className="iconfont icon-Shapearrow-"></i>
                            </div>
                            <input type="text" placeholder="//" className="estimate-main-input"  onKeyUp={this.reachValue}></input>
                            <button className="search-box" 
                                    onClick={()=>{    
                                    selloutStore.getselloutList({
                                        storeDay:selloutStore.datetime,
                                        categoryID:selloutStore.lastcombination.lastid === "" 
                                                    ? '0'
                                                    : selloutStore.lastcombination.lastid,
                                        searchContent:this.state.recordValue
                                    });

                                }}> 
                                <i className="iconfont icon-order_btn_search" ></i>
                            </button>
                            {this.state.Commodity}
                        </div>
                    </div> 
                    <div className="main-block-history">
                        <div className="main-block">
                            <div className="estimate-list-button">
                                <div className="estimate-list-top"> 
                                            <span></span>
                                            <span></span>
                                            <span></span>
                                            <span></span>
                                            <span></span>
                                            <span></span>
                                            <span></span>
                                            <span></span>
                                            <span></span> 
                                            <span></span>
                                            <span></span>
                                            <span></span>
                                            <span></span>
                                </div>
                                <div className="estimate-list"> 
                                        <div className="estimate-scroll">
                                            <MyScroll >  
                                                  {selloutStore.selloutList.map((sellout,index) => { 
                                                        return <SelloutLb   key={index} 
                                                                            selloutStore={selloutStore} 
                                                                            sellout={sellout}  
                                                                            handleClick={this.clicksellout.bind(this, sellout)}
                                                                />
                                                    })}  
                                            </MyScroll>
                                        </div>
                                </div> 
                                {this.state.isnodl === 'false'    &&////////
                                    <div className="normal-button">
                                        <button 
                                            onClick={()=>{  
                                                let flagt;
                                                selloutStore.selloutList.map((sellout,index)=>{
                                                    if(sellout.selected === true){
                                                        flagt=true;
                                                    }
                                                    return sellout;
                                                });

                                                if(flagt !== true){
                                                    message.warn("");     
                                                }else{
                                                    this.setState({ZjEstimatePopup: 
                                                                    <ZjEstimatePopup 
                                                                            updateestime={()=>{
                                                                                this.setState({ZjEstimatePopup:''})
                                                                            }} 
                                                                            okbutton={()=>{    
                                                                                this.setState({ZjEstimatePopup:''}); 
                                                                            }}>
                                                                    </ZjEstimatePopup>
                                                                  }); 
                                                    selloutStore.getaddorreduce("1");// 
                                                    selloutStore.getreasonlist(selloutStore.addorreduce);//
                                                    this.props.selloutStore.reasontext.reasonName ="";//
                                                    this.props.selloutStore.issuccess ='';//

                                                }
                                            
                                            }}>
                                        </button>
                                        <button  
                                            onClick={()=>{ 
                                                    let flagt;
                                                    selloutStore.selloutList.map((sellout,index)=>{
                                                        if(sellout.selected === true){
                                                            flagt=true;
                                                        }
                                                        return sellout;
                                                    });
                                                    if(flagt !== true){
                                                        message.warn("");     
                                                    }else{
                                                        this.setState({ZjEstimatePopup:
                                                                       <ZjEstimatePopup 
                                                                            updateestime={()=>{
                                                                                this.setState({ZjEstimatePopup:''})
                                                                            }} 
                                                                            okbutton={()=>{   
                                                                                this.setState({ZjEstimatePopup:''}); 
                                                                            }}>
                                                                        </ZjEstimatePopup>
                                                                      });

                                                        selloutStore.getaddorreduce("2");//
                                                        selloutStore.getreasonlist(selloutStore.addorreduce);//
                                                        this.props.selloutStore.reasontext.reasonName ="";//
                                                        this.props.selloutStore.issuccess ='';//
                                                    } 
                                                }}>
                                        
                                        </button>
                                        <button 
                                            onClick={()=>{ 
                                                let flagt;
                                                selloutStore.selloutList.map((sellout,index)=>{
                                                    if(sellout.selected === true){
                                                        flagt=true;
                                                    }
                                                    return sellout;
                                                }); 
                                                if(flagt !== true){
                                                    message.warn("");     
                                                }else{
                                                    this.setState({ZjEstimatePopup:
                                                                  <ZjEstimatePopup 
                                                                        updateestime={()=>{
                                                                            this.setState({ZjEstimatePopup:''})
                                                                        }}  
                                                                        okbutton={()=>{ 
                                                                            this.setState({ZjEstimatePopup:''}); 
                                                                        }}>
                                                                  </ZjEstimatePopup>
                                                                  })
                                                    selloutStore.getaddorreduce("3");//
                                                    selloutStore.getreasonlist(selloutStore.addorreduce);//
                                                    this.props.selloutStore.reasontext.reasonName ="";//
                                                    this.props.selloutStore.issuccess ='';//
                                                }
                                            }}>
                                        </button>
                                        <button 
                                            onClick={()=>{
                                                let flagt;
                                                selloutStore.selloutList.map((sellout,index)=>{
                                                    if(sellout.selected === true){
                                                        flagt=true;
                                                    }
                                                    return sellout;
                                                });


                                                if(flagt !== true){
                                                        message.warn("");     
                                                }else{
                                                        this.setState({GqEstimate:
                                                                       <GqEstimatePopup 
                                                                            gqclose={()=>{
                                                                                this.setState({GqEstimate:''});
                                                                            }} 
                                                                            okbutton={()=>{ 
                                                                                this.setState({GqEstimate:''}); 
                                                                            }}>
                                                                      </GqEstimatePopup>
                                                                      })
                                                        selloutStore.getaddorreduce("4");//
                                                        selloutStore.getreasonlist(selloutStore.addorreduce);//
                                                        this.props.selloutStore.reasontext.reasonName ="";//
                                                        this.props.selloutStore.issuccess ='';// 
                                                }
                                            }}>
                                        </button>
                                        
                                        <button 
                                            onClick={()=>{
                                                this.setState({
                                                    addmune:
                                                    <div>
                                                        <div className="mask" 
                                                            onClick={()=>{
                                                            this.setState({addmune:''});
                                                            }}>  
                                                        </div> 
                                                        <div className="mask-conten">
                                                            <RightMemu whatpopup={()=>{
                                                                this.setState({addmune:''});
                                                            }}></RightMemu>
                                                        </div> 
                                                    </div>
                                                    })
                                            }}>
                                        </button> 
                                        <button onClick={this.operatehistory}></button> 
                                    </div>
                                }

                                {this.state.isnodl === 'true' && //
                                <div className="history_botton">
                                    <button  
                                        onClick={()=>{ 
                                            this.setState({Import:
                                                            <ImportPopup 
                                                                importclose={()=>{
                                                                    this.setState({Import:''});
                                                                }} 
                                                                importok={()=>{ 
                                                                    selloutStore.implementImport(selloutStore.datetime,selloutStore.Importtype);//action
                                                                    this.setState({Import:''});
                                                                }}>
                                                            </ImportPopup>
                                                        });
                                        }}>
                                    </button>
                                </div>
                                } 

                                {this.state.ZjEstimatePopup} 
                                {this.state.GqEstimate}
                                {this.state.Menu}
                                {this.state.Import}
                                {this.state.addmune}
                                
                            </div>
                        </div>
                    </div>
                </div>
                {operatePrompt}
            </div>

            
 
    );
  }
}

EstimateClearTotal.wrappedComponent.contextTypes = {
	router: React.PropTypes.object.isRequired
}



export default EstimateClearTotal;



