import React from 'react';
import {Modal} from 'antd'; 
import { observer, inject } from 'mobx-react'; 
import './import-popup.less';

 
//
@inject('selloutStore') @observer
class ImportPopup extends React.Component {

  state={
    ischeck:'iconfont icon-icon_checkbox_sel',
    nocheck:'iconfont icon-yuan'
  }

 //
 handleCancel=()=>{
   if(this.props.importclose){
     this.props.importclose();
   }

 }

 //
 handleOk=()=>{
  if(this.props.importok){
     this.props.importok();
   }

 }
 //
 oncheckedqb=()=>{
   this.setState({
      ischeck:'iconfont icon-icon_checkbox_sel',
      nocheck:'iconfont icon-yuan'
   });
   this.props.selloutStore.setImporttype(1);

 }
 //
 oncheckedzj=()=>{
  this.setState({
      ischeck:'iconfont icon-yuan',
      nocheck:'iconfont icon-icon_checkbox_sel'
   });
   this.props.selloutStore.setImporttype(2);
 }
 render() {

    return (
       <div>
          <Modal title=""
              visible={true}
              closable={false}
              maskClosable={false}
              onCancel={this.handleCancel}
              onOk={this.handleOk}
              width={600} wrapClassName="import-popup-modal"
            >
            <div className="import-conten">
              <p> <i className={this.state.ischeck}  onClick={this.oncheckedqb}></i> <span></span> <em></em></p>
              <p> <i className={this.state.nocheck}  onClick={this.oncheckedzj}></i> <span></span> <em></em></p> 
            </div>
          </Modal>

       </div>
    );
  }
}



export default ImportPopup;



