import React from 'react';
import {Modal} from 'antd';
import { inject, observer } from 'mobx-react';  
import './update-estimate-popup.less';
import CommonKeyboardNum from '../common-keyboard-num';//

 
//
@inject('selloutStore') @observer
class UpdateEstimatePopup extends React.Component { 
  state={  
    type:this.props.selloutStore.optionIDSum, // 0  1 1(2)  0 
    current:'0', // 1 
    isCureSale:'true'//  1 ,2   //true 
  }
 
 //
 handleCancel=()=>{  
   if(this.props.updateestime){
     this.props.updateestime();
   }
 }
 
  

 //
 handleOk=()=>{      
    let  selloutStore=this.props.selloutStore;
    let  messageobj=selloutStore.messageobj; 
    let  messagelist=selloutStore.selloutmessageList;  
    let curbSaleList=[];
    messagelist.map(function(obj){  
      if(messageobj.needWeigh){
        curbSaleList.push({"optionID":obj.optionID,"quantity":obj.initFloatQuantity}); 
      }else{
        curbSaleList.push({"optionID":obj.optionID,"quantity":obj.initQuantity});
      }
      return obj;
    }); 
    let menuID=selloutStore.savemenuID;
    let  isAccOptionCureSale;//false
    if(Number(this.state.type) === 1 || Number(this.state.type) === 2 ){//1 2 
      isAccOptionCureSale=this.state.isCureSale;
    }else{//0 
      isAccOptionCureSale=false;
    }  
    console.log(curbSaleList);
    this.props.selloutStore.addmessagevalue(curbSaleList,menuID,isAccOptionCureSale);//action
     
    if(this.props.okdateestime){
      this.props.okdateestime();
    }

 }  
  render() { 
    let  selloutStore=this.props.selloutStore;
    let  messagelist=selloutStore.selloutmessageList; 
    let  messageobj=selloutStore.messageobj; 
    
    return (
       <div>
          <Modal title={selloutStore.menuname}
              visible={true} 
              closable={false}
              maskClosable={false} 
              onCancel={this.handleCancel}
              onOk={this.handleOk}  
              width={840} wrapClassName="update-estimate-popup-modal"  
            >
           <div className="update-estimate-left">
              {Number(this.state.type) === 1 && //1
              <div className="update-left-content-yesan"> 
                <div className="update-left-how">
                  <p className="update-how-span"> {messagelist.length}  </p> 
                   {(() =>{ 
                      if(messagelist.length) {
                          let button;
                          let standard;
                          let reservenum;
                          let estimatenum;
                          messagelist.forEach((mnews,index) => {    
                              if(mnews.exchangeRate === 1){  
                                  standard = mnews.optionName;//
                                  reservenum = mnews.bookingSum;//
                                  estimatenum = messageobj.needWeigh ? mnews.initFloatQuantity : mnews.initQuantity;//
                                  selloutStore.setvalue(standard,reservenum,estimatenum);//store  2 
                                  selloutStore.subscript=index;//json/
                                  button = <p className="update-how-botton">
                                              <i className="iconfont icon-icon_checkbox_sel-" onClick={()=>{ 
                                                  this.setState({
                                                      type:'2',//2
                                                    isCureSale:'false'
                                                  });  
                                              }} ></i>
                                              
                                           </p>
                              }
                          }); 
                          return button; 
                      }
                   })()}  
                </div>
                  {(() =>{ 
                      if(messagelist.length) {
                          return messagelist.map((mnews,index) => { 
                            let   quantity = messageobj.needWeigh ? messagelist[index].initFloatQuantity : messagelist[index].initQuantity;
                            return  <div key={index} className="update-left-each">
                                      <p className="update-left-type">{mnews.optionName}</p>
                                      <div className="update-left-number">
                                        <span><em>{mnews.bookingSum}</em></span>
                                        <span>
                                           
                                          <input 
                                            type="text"  
                                            value={quantity || ''}
                                            readOnly
                                            onClick={(e)=>{ 
                                              this.setState({current:index});  
                                            }}
                                          />  
                                        </span>
                                      </div>
                                    </div> 
                          });
                      }
                   })()}    
              </div>
              }
              {Number(this.state.type) === 2 && //2
              <div className="update-left-content-noan">
                  <div className="update-left-how">
                    <p className="update-how-span">{selloutStore.standard} </p>
                    <p className="update-how-botton">
                      <i className="iconfont icon-icon_checkbox_sel-"  
                        onClick={()=>{
                            this.setState({
                              type:'1',//1
                              isCureSale:'true'
                            }) 
                        }}>
                      </i> 
                    </p>
                  </div>
                  <div className="update-left-each">  
                    <span></span>
                    <input type="text"  className="readOnly-bg"  value={selloutStore.reservenum}  readOnly/>
                 </div> 
                  <div className="update-left-each">  
                    <span></span>
                    <input type="text"  value={selloutStore.estimatenum || ''}   readOnly 
                      />
                 </div>
              </div>
              }
            
              {Number(this.state.type) === 0 && //0 
                <div className="update-left-content-noguige">  
                  {(() =>{ 
                      if(messagelist.length) {
                          return messagelist.map((mnews,index) => {    
                            let quantity = messageobj.needWeigh ? messagelist[index].initFloatQuantity : messagelist[index].initQuantity;
                            return <div key={index}>  
                                      <div className="update-left-each">  
                                          <span></span><input className="readOnly-bg"  type="text"   value={messagelist[index].bookingSum} readOnly />
                                      </div> 
                                      <div className="update-left-each">  
                                          <span></span>
                                          <input 
                                              type="text"   
                                              value={quantity || ''}
                                              readOnly
                                            />  
                                      </div>
                                    </div>
                          });
                      }
                      })()}   
                </div>
              }
           </div>
           <div className="update-estimate-right">
            <div className="update-right-keyboard">
              <CommonKeyboardNum width={382} height={364} Whetherpoint={messageobj.needWeigh ? true : false} getvalue={(value)=>{ 
                  
                  let current = this.state.current; //1  
                  let initFloatQuantity = messagelist[current].initFloatQuantity || ''; // 1   =true 
                  let initQuantity =messagelist[current].initQuantity || ''; // 1   =false 

 
                  let estimatenum = selloutStore.estimatenum || '';//2  true/false
 
                  let initFloatQuantity1 =  messagelist[current].initFloatQuantity || ''; //0   =true 
                  let initQuantity1 = messagelist[current].initQuantity || ''; //0   =false 
                    
                  if(Number(this.state.type) === 1 ){ //1   
                     if(value === "click"){ //    
                        if(messageobj.needWeigh){//
                            messagelist[current].initFloatQuantity = (initFloatQuantity+'').substring(0,(initFloatQuantity+'').length-1); 
                        }else{
                           messagelist[current].initQuantity = (initQuantity+'').substring(0,(initQuantity+'').length-1);
                        } 
                      }else{ //
                        if(messageobj.needWeigh){// 
                              messagelist[current].initFloatQuantity = initFloatQuantity+value; 
                        }else{ 
                             messagelist[current].initQuantity = initQuantity+value;  
                           
                        }
                      } 
                  }else if(Number(this.state.type) === 2){ //2  
                       if(value === "click"){  //       
                            if(messageobj.needWeigh){  //
                               messagelist[selloutStore.subscript].initFloatQuantity =(estimatenum+'').substring(0,(estimatenum+'').length-1); 
                            }else{ 
                              messagelist[selloutStore.subscript].initQuantity =(estimatenum+'').substring(0,(estimatenum+'').length-1); 
                            }
                            selloutStore.estimatenum  = (estimatenum+'').substring(0,(estimatenum+'').length-1); 
                              
                        }else{ // 
                            if(messageobj.needWeigh){ // 
                                  messagelist[selloutStore.subscript].initFloatQuantity = estimatenum+value;   
                            }else{  
                                  messagelist[selloutStore.subscript].initQuantity = estimatenum+value;  
                             
                            }  
                             selloutStore.estimatenum=estimatenum+value;
                        }
                       
                  }else if(Number(this.state.type) === 0){//0 
                        if(value === "click"){  // 
                            if(messageobj.needWeigh){ //
                              messagelist[current].initFloatQuantity = (initFloatQuantity1+'').substring(0,(initFloatQuantity1+'').length-1);  
                            }else{
                              messagelist[current].initQuantity = (initQuantity1+'').substring(0,(initQuantity1+'').length-1);    
                            }
                        }else{ //
                              if(messageobj.needWeigh){//
                                messagelist[current].initFloatQuantity = initFloatQuantity1+value; 
                              }else{
                                messagelist[current].initQuantity = initQuantity1+value;
                              }
                                
                        } 
                  }         
              }}>
              </CommonKeyboardNum>
            </div>
           </div>
          </Modal> 
       </div>
    );
  }
}
 
export default UpdateEstimatePopup;


